import commonList from '@/components/commom/common-list.vue'
import loadMore from '@/components/commom/load-more.vue'
import { demo } from './data.js'
export default {
	components: {
		commonList,
		loadMore
	},
	data() {
		return {
			// 列表高度
			scrollH: 600,
			// 顶部选项卡
			scrollInto: '',
			tabIndex: 0,
			tabBars: [
				{
					name: '关注',
				},
				{
					name: '推荐',
				},
				{
					name: '体育',
				},
				{
					name: '热点',
				},
				{
					name: '财经',
				},
				{
					name: '娱乐',
				},
				{
					name: '军事',
				},
				{
					name: '历史',
				},
				{
					name: '本地',
				},
			],
			newsList: []
		}
	},
	// 监听顶级导航搜索框
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search'
		});
	},
	// 监听导航按钮点击事件
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url: '../add-input/add-input'
		});
	},
	onLoad() {
		this.scrollHeight()
		this.getData()
	},
	methods: {
		// 获取列表数据
		getData() {
			let arr = []
			for (let i = 0; i < this.tabBars.length; i++) {
				let obj = {
					loadmore: '上拉加载更多',
					list: []
				}
				if(i < 2) {
					obj.list = demo
				}
				arr.push(obj)
			}
			this.newsList = arr
		},
		// 获取屏幕高度
		async scrollHeight() {
			let res = await uni.getSystemInfoSync()
			this.scrollH = res.windowHeight - uni.upx2px(100)
		},
		// 监听活动
		onChangeTab(e) {
			this.changTab((e.detail.current))
		},
		// 切换选项
		changTab(index) {
			if(this.tabIndex === index) return
			 this.tabIndex = index
			 // 滚动到指定元素
			 this.scrollInto = 'tab' + index
		},
		// 关注
		follow(index) {
			console.log(index)
			this.list[index].isFollow = !this.list[index].isFollow
			uni.showToast({
				title: '关注成功'
			})
		},
		doSupport(data) {
			console.log(data)
			let { type, index } = data
			// 拿到当前对象
			let item = this.list[index].support
			let msg = type === 'support' ? '顶' : '踩'
			// 之前没有操作
			if(item.type === '') {
				item[type + '_count']++
			} else if(item.type === 'support' && type === 'unsupport' ) { // 之前顶了
				// 踩 + 1
				item.unsupport_count++
				// 顶 + 1
				item.support_count--
			} else if(item.type === 'unsupport' && type === 'support') { // 之前踩了
				// 顶 + 1
				item.support_count++
				// 踩 + 1
				item.unsupport_count--
			}
			item.type = type
			uni.showToast({
				title: msg + '成功'
			})
		},
		// 下拉加载
		loadmore(index) {
			let item = this.newsList[index]
			item.loadmore = '加载中...'
			setTimeout(() => {
				item.list = [...item.list, ...item.list]
				item.loadmore = '上拉加载更多'
			}, 2000)
		}
	}
}
